const _hoisted_58 = /*#__PURE__*/_createStaticVNode("<li data-v-109f95d0>ローテーションの長さ<br data-v-109f95d0> ローテーションに存在する元素スキルのクールタイムまたは元素爆発のクールタイムから決定します。 元素スキルの発動回数は考慮しませんが、元素爆発の方は発動回数に応じて加算します。 初期値は20秒。 </li><li data-v-109f95d0>出場時間<br data-v-109f95d0> ローテーションの内容から大雑把に計算して割り当てます。 キャラクター毎アクション毎の所要時間のデータを持たないため、正確ではありません。 </li><li data-v-109f95d0>大部分のキャラクターの元素スキル<br data-v-109f95d0> 元素スキル命中時に元素粒子が発生するタイプの元素スキルは、直後のアクション実行者が元素粒子を獲得します。 </li><li data-v-109f95d0>胡桃、宵宮、神里綾人、放浪者 等の元素スキル<br data-v-109f95d0> 元素スキル使用時に元素粒子が発生せず、その後の通常攻撃または重撃命中時に元素粒子が発生するタイプの元素スキルは、継続時間一杯攻撃した場合に発生するであろう数の元素粒子を実行者自身が獲得します。 </li><li data-v-109f95d0>フィッシュル、鍾離、アルベド、八重神子 等の元素スキル<br data-v-109f95d0> 存在時間の長い設置物からの攻撃命中時に元素粒子が発生するタイプの元素スキルは、ローテーションまたは継続時間の間ずっと攻撃し続けた場合に発生するであろう数の元素粒子を出場時間を基準に全員に按分します。 また、設置系でない雷電将軍とナヒーダも同様の扱いとします。 </li><li data-v-109f95d0>アンバー、香菱、モナ、甘雨 等の元素スキル<br data-v-109f95d0> 存在時間の短い設置物からの攻撃命中または設置物が破壊された時に元素粒子が発生するタイプの元素スキルについても同様に出場時間を基準に全員に按分するが、存在時間の短さから実戦との乖離が大きくなります。(妥協) </li><li data-v-109f95d0>雷元素共鳴が生成する元素粒子<br data-v-109f95d0> ローテーションの間クールタイムの5秒間隔で雷元素関連反応が起き続けた場合に発生するであろう数の元素粒子を出場時間を基準に全員に按分します。 チーム内に対象となる元素反応を起こせる元素のキャラクターが存在しない場合は0。 </li><li data-v-109f95d0>西風武器が生成する元素粒子<br data-v-109f95d0> 使用者の出場回数および出場時間(%)、ローテーションの長さから武器効果をトリガーする回数を決定します。 トリガー回数はローテーションの長さを武器効果のクールタイムで割った数までとします。 元素粒子は基本的に装備者が獲得するが、装備者が1アクションだけで退場する場合は次のアクション実行者が獲得します。 </li><li data-v-109f95d0>通常攻撃、重撃命中時に獲得する元素エネルギー<br data-v-109f95d0> 通常攻撃または重撃の回数に応じて元素エネルギーを獲得します。 元素エネルギーを獲得する攻撃回数は武器タイプ毎に異なります。 </li>", 9)
